html{height:100%}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;color:white;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;height:100%;background:linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);background-size:400% 400%;-webkit-animation:Gradient 20s ease infinite;animation:Gradient 20s ease infinite;height:100%}#root{height:100%}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}@-webkit-keyframes Gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@keyframes Gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}


.weather-card{position:relative;background-color:#423838;color:#fff;border-radius:10px 0 10px 10px;padding:10px 20px;text-align:center;margin-top:25px;transition:all .1s linear}.weather-card:hover{background-color:#fff;color:#423838}.weather-card:hover .weather-card__time{background-color:#fff;color:#423838}.weather-card__time{font-size:16px;font-weight:600;position:absolute;width:100px;text-align:center;height:25px;top:-25px;right:0;background-color:#423838;border-radius:10px 10px 0 0;transition:all .1s linear}.weather-card__temp{font-size:30px}.weather-card__image{width:100%;max-width:100px}

.weather-days-slider{padding-bottom:20px}.weather-day{margin:30px 0}.weather-day__name{font-size:25px;text-transform:capitalize}

.wrapper-preloader{text-align:center}.lds-ripple{display:inline-block;position:relative;width:280px;height:280px}.lds-ripple div{position:absolute;border:5px solid #fff;opacity:1;border-radius:50%;-webkit-animation:lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;animation:lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite}.lds-ripple div:nth-child(2){-webkit-animation-delay:-0.5s;animation-delay:-0.5s}@-webkit-keyframes lds-ripple{0%{top:135px;left:135px;width:0;height:0;opacity:1}100%{top:0px;left:0px;width:270px;height:270px;opacity:0}}@keyframes lds-ripple{0%{top:135px;left:135px;width:0;height:0;opacity:1}100%{top:0px;left:0px;width:270px;height:270px;opacity:0}}

